"use client";

import Link from "next/link";
import { Avatar, AvatarFallback, AvatarImage } from "~/components/ui/avatar";
import { Button } from "~/components/ui/button";
import { Badge } from "~/components/ui/badge";
import { formatRelativeTime, getInitials } from "~/lib/utils";
import { ExternalLink } from "lucide-react";
import { EditorContent } from "~/components/editor/Editor";
import { ReactionButton } from "~/components/reactions/ReactionButton";
import { AttachmentGallery } from "./AttachmentGallery";
import type { SerializedEditorState } from "lexical";

interface PostDetailProps {
  post: {
    id: string;
    title: string | null;
    content: unknown;
    liveUrl: string | null;
    createdAt: Date;
    author: {
      id: string;
      displayName: string;
      avatarUrl: string | null;
    };
    attachments: Array<{
      id: string;
      type: string;
      url: string;
      filename: string;
      mimeType: string;
      size: number;
      thumbnailUrl: string | null;
      width: number | null;
      height: number | null;
    }>;
    projects: Array<{
      project: {
        id: string;
        name: string;
      };
    }>;
    reactions: Array<{
      type: string;
      userId: string;
      user: {
        id: string;
        displayName: string;
      };
    }>;
    _count: {
      comments: number;
    };
  };
}

export function PostDetail({ post }: PostDetailProps) {
  return (
    <article className="space-y-6">
      {/* Header */}
      <header className="space-y-4">
        {/* Author info */}
        <div className="flex items-center gap-3">
          <Link href={`/user/${post.author.id}`}>
            <Avatar className="h-12 w-12">
              <AvatarImage src={post.author.avatarUrl ?? undefined} />
              <AvatarFallback>{getInitials(post.author.displayName)}</AvatarFallback>
            </Avatar>
          </Link>
          <div>
            <Link
              href={`/user/${post.author.id}`}
              className="font-medium hover:underline"
            >
              {post.author.displayName}
            </Link>
            <p className="text-sm text-muted-foreground">
              {formatRelativeTime(new Date(post.createdAt))}
            </p>
          </div>
        </div>

        {/* Title */}
        {post.title && (
          <h1 className="text-3xl font-bold tracking-tight">{post.title}</h1>
        )}

        {/* Projects */}
        {post.projects.length > 0 && (
          <div className="flex flex-wrap gap-2">
            {post.projects.map(({ project }) => (
              <Link key={project.id} href={`/projects/${project.id}`}>
                <Badge variant="secondary" className="hover:bg-secondary/80">
                  {project.name}
                </Badge>
              </Link>
            ))}
          </div>
        )}
      </header>

      {/* Content */}
      <div className="prose prose-neutral dark:prose-invert max-w-none">
        <EditorContent content={post.content as SerializedEditorState} />
      </div>

      {/* Attachments */}
      {post.attachments.length > 0 && (
        <AttachmentGallery attachments={post.attachments} />
      )}

      {/* Footer */}
      <footer className="flex items-center justify-between border-t pt-4">
        <div className="flex items-center gap-4">
          <ReactionButton
            postId={post.id}
            reactions={post.reactions}
            count={post.reactions.length}
          />
          <span className="text-sm text-muted-foreground">
            {post._count.comments} comments
          </span>
        </div>

        {post.liveUrl && (
          <Button variant="outline" asChild>
            <a
              href={post.liveUrl}
              target="_blank"
              rel="noopener noreferrer"
              className="gap-2"
            >
              View live
              <ExternalLink className="h-4 w-4" />
            </a>
          </Button>
        )}
      </footer>
    </article>
  );
}
